.form {
  display: flex;
  flex-direction: column;
}

.field {
  margin: 0 5px;
  font-size: 16px;
}

.select {
  margin: 0 5px;
  font-size: 16px;
}

.errorMessage {
  all: initial;
  display: block;
  margin: 0px 5px 5px;
}

.search_submit {
  width: 40%;
  align-self: center;
  margin: 10px 0;
  padding: 3px 5px;
  box-shadow: 1px 2px darkslategrey;
  border-radius: 2px;
}
